import React from 'react'
import ReactDOM from 'react-dom'

import Item from '../item/index.js'
import '../item/item.scss'
import image_404 from '../assets/images/image-not-found.jpg'

var currentItemToken = 11;

function itemCreate(name, description, price) {
    currentItemToken++;
    var el = document.createElement('div');
    el.id = 'div' + currentItemToken;
    ReactDOM.render(
        <Item key={currentItemToken} itemNumber={currentItemToken} createQuantity={0} createName={name} createDescription={description} createPrice={price} />, el
    )
    return el
};

const itemsAdded = props => {
    return (
        <div>
            <div className="center item item-border">
                <img src={image_404} alt="404, Resource not found" className="display_size" />
                <div className="description">
                    <input id="nameText" type="text" placeholder="Sample Header" className="poppins header_font"></input>
                    <input id="descText" type="text" placeholder="Sample description." className="poppins description_font"></input>
                </div>
                <div className="price right">
                    <input id="priceText" type="text" placeholder="$34.99" className="poppins header_font"></input>
                </div>
            </div>
            <div className="button-padding">
                <div className="button-layout right">
                    <button className="poppins item-layout" type="button" onClick={() => {
                        let name = document.getElementById('nameText').value;
                        let description = document.getElementById('descText').value;
                        let price = document.getElementById('priceText').value;
                        if (name == '' || description == ''|| price == '') {
                            alert('Please fill valid user entries!');
                        }
                        else {
                            let el = itemCreate(name, description, price);
                            document.getElementById('itemList').appendChild(el);
                            // Have itemList view always appear at the bottom when appending a new item.
                            document.getElementById('itemList').scrollTop = document.getElementById('itemList').scrollHeight;
                        }
                    }}>Add Item</button>
                    <button className="poppins item-layout" type="button">Checkout</button>
                </div>
            </div>
        </div>
    )
};

export default itemsAdded;